import gameContext from '../../../gameContext';
import { useContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import socketService from '../../../services/socketService';
import toolkitService from '../../../services/toolkitService';
export function ToolkitAfter() {
  const {
    isProtagonist,
    isRoomName,
    textarea1,
    setTextarea1,
    textarea2,
    setTextarea2,
    textarea3,
    setTextarea3,
  } = useContext(gameContext);

  const [rendering, setRendring] = useState<boolean>(true);
  const [str, setStr] = useState('');
  const [str1, setStr1] = useState('');
  const [str2, setStr2] = useState('');
  const history = useNavigate();

  let t1: any[] = [];
  let t2: any[] = [];
  let t3: any[] = [];

  // Onchange 함수
  const handleOnchange = (e: React.ChangeEvent<any>) => {
    setRendring(false);
    const value = e.target.value;
    switch (e.target.id) {
      case 'textarea1':
        setStr(value);
        break;
      case 'textarea2':
        setStr1(value);
        break;
      case 'textarea3':
        setStr2(value);
        break;
      default:
        break;
    }
  };

  // 사후툴킷 내용 서버에 전송
  const onChangeWrite = (id: string, contents: string) => {
    if (isProtagonist) {
      if (socketService.socket) {
        toolkitService.onChangeWrite(socketService.socket, id, contents);
        setRendring(true);
        setStr('');
        setStr1('');
        setStr2('');
      }
    }
  };

  // 사후툴킷 내용 서버에서 받아오기

  // 툴킷 완료
  const finishToolkit = () => {
    if (socketService.socket)
      toolkitService.finishToolkit(socketService.socket, {
        roomName: isRoomName,
        before: false,
        text1: textarea1,
        text2: textarea2,
        text3: textarea3,
      });
    history('/');
  };

  // 사후 툴킷 내용 출력함수
  const display = (array: Array<string>) => {
    if (array.length === 0) return;
    else {
      switch (array) {
        case textarea1:
          for (var i = 0; i < array.length; i++) {
            t1.push(
              <>
                {array[i]}
                <br />
              </>
            );
          }
          return t1;
        case textarea2:
          for (var j = 0; j < array.length; j++) {
            t2.push(
              <>
                {array[j]}
                <br />
              </>
            );
          }
          return t2;
        case textarea3:
          for (var k = 0; k < array.length; k++) {
            t3.push(
              <>
                {array[k]}
                <br />
              </>
            );
          }
          return t3;
        default:
          break;
      }
    }
  };

  // 초마다 변화되는 내용 refresh
  useEffect(() => {
    const check = () => {
      if (socketService.socket) {
        toolkitService.updateWrite(socketService.socket, (id, contents) => {
          switch (id) {
            case 'textarea1':
              setTextarea1(textarea1.concat(contents));
              console.log(textarea1);
              break;
            case 'textarea2':
              setTextarea2(textarea2.concat(contents));
              break;
            case 'textarea3':
              setTextarea3(textarea3.concat(contents));
              break;
            default:
              break;
          }
        });
      }
    };
    if (rendering) {
      check();
    }
  });

  return (
    <div className="ToolkitAfterpage">
      <div className="ToolkitAfterpageGrid">
        <div className="ToolkitAfterpageGridS">
          <div style={{ width: '630px' }}>
            <div className="ToolkitAfterpageText">
              Q. 사회문제를 해결하는데에 각 직업의 역할은 무엇이었나요?
            </div>
            <div style={{ display: 'flex', width: '630px' }}>
              <input
                className="ToolkitAfterpageInput"
                id="textarea1"
                value={str}
                type="text"
                onChange={handleOnchange}
              />
              <div
                className="ToolkitAfterpagebtn"
                onClick={() => onChangeWrite('textarea1', str)}
              >
                답변하기
              </div>
            </div>

            <div className="ToolkitpageAfterTextArea1">
              {display(textarea1)}
            </div>
          </div>
        </div>
        <div className="ToolkitAfterpageGridS">
          <div style={{ borderLeft: ' 1px solid #0CC0FD' }}>
            <div className="ToolkitAfterpageText">
              Q. 플랜빌리지 게임 진행 시, 나만의 전략이 있었나요?
            </div>
            <div style={{ display: 'flex', width: '630px' }}>
              <input
                className="ToolkitAfterpageInput"
                id="textarea2"
                value={str1}
                type="text"
                onChange={handleOnchange}
              />
              <div
                className="ToolkitAfterpagebtn"
                onClick={() => onChangeWrite('textarea2', str1)}
              >
                답변하기
              </div>
            </div>
            <div className="ToolkitpageAfterTextArea2">
              {display(textarea2)}
            </div>
          </div>
          <div
            style={{
              borderLeft: ' 1px solid #0CC0FD',
              borderTop: '1px solid #0CC0FD',
            }}
          >
            <div className="ToolkitAfterpageText">
              Q. 게임진행 시, 어려웠던 점은 무엇이었나요?
            </div>
            <div style={{ display: 'flex', width: '630px' }}>
              <input
                className="ToolkitAfterpageInput"
                id="textarea3"
                value={str2}
                type="text"
                onChange={handleOnchange}
              />
              <div
                className="ToolkitAfterpagebtn"
                onClick={() => onChangeWrite('textarea3', str2)}
              >
                답변하기
              </div>
            </div>
            <div className="ToolkitpageAfterTextArea2">
              {display(textarea3)}
            </div>
            {textarea1.length > 3 &&
            textarea2.length > 3 &&
            textarea3.length > 3 ? (
              <div
                className="Toolkitpagebtn"
                onClick={() => finishToolkit()}
                style={{ marginLeft: '600px' }}
              />
            ) : (
              ''
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
