import { Socket } from "socket.io-client";
class RoomService {
  
  // Plan: village 방 입장
  public async joinRoom(socket: Socket, roomId: string ,isUserName:string,isUserNum:number,option:string): Promise<boolean> {
    return new Promise((rs, rj) => {
      //게임
    if(option === 'game'){
        socket.emit("join_game", {roomId, isUserName,isUserNum} );
        socket.on("game_joined", () => rs(true));
        socket.on("game_join_error", ({ error }) => rj(error));
    }
    // 툴킷
    else if(option === 'toolkit'){
        socket.emit("join_toolkit", {roomId, isUserName,isUserNum} );
        socket.on("toolkitBefore_joined", () => rs(true));
        socket.on("toolkitAfter_joined", () => rs(false));
        socket.on("toolkit_join_error", ({ error }) => rj(error));
    }
    else{
        socket.on("join_error", ({error}) => rj(error));
    }
    });
  }

  // 접속 종료
  public disconnect(socket: Socket,userName:string,roomId: string): Promise<boolean> {
    return new Promise((rs, rj) => {
      socket.emit("disconnect_Server",{userName,roomId});
      socket.on("Bye", () => rs(true));
      socket.on("disconnect_Server_error", ({ error }) => rj(error));
    });
  }

  //접속 후 player 정보 보내기
  public async setPlayer
  (socket: Socket,data:any):Promise<boolean>{ 
    return new Promise((rs,rj)=> {
      socket.emit('setplayer_Data', {data});
      socket.on("create_Player",()=>rs(true))
      socket.on("create_Player_error", ({ error }) => rj(error));
    })
    
  }


}

export default new RoomService();