import { Socket } from "socket.io-client";
class OtherService {

  // 만족도 조사 결과 보내기
  public async sendCSAT(socket: Socket, id: string, roomid:string,  data: Array<any>,) {
    socket.emit("send_CSAT", { id, roomid, data });
  }

  // 순위표 불러오기
  public async callBoard(socket : Socket,data:any){
    socket.emit("call_Board", data)
  }
  
  // 순위표 받아오기
  public async loadBoard(socket:Socket,listiner: (data:any) =>void){
    socket.on("load_Board",({data})=>listiner(data))
  }

  // 완료된 툴킷 정보 받아오기
  public async LoadToolkit(socket: Socket,listiner:(toolkit:any,team:any)=>void){
    socket.on("Load_toolkit", ({toolkit, team})=>listiner(toolkit,team));
 }
}

export default new OtherService();