import coinA from '../../Img/coinA.png';
import coinC from '../../Img/coinC.png';
import coinE from '../../Img/coinE.png';
import coinL from '../../Img/coinL.png';
import coinM from '../../Img/coinM.png';
import coinS from '../../Img/coinS.png';
import Swal from 'sweetalert2';

// 장애물 도착 시 가져오지 못하는 코인 출력 함수
export const disasterToCoin = (input: number) => {
  switch (input) {
    case 1:
      return '도전력';
    case 2:
      return '창의력';
    case 3:
      return '판단력';
    case 4:
      return '분석력';
    case 5:
      return '협동력';
    case 6:
      return '공감력';
    default:
      return '몰?루';
  }
};

// 장애물 도착 시 장애물 유형 출력 함수
export const disasterType = (input: number, input2: number) => {
  switch (input) {
    case 1:
      return Swal.fire({
        icon: 'warning',
        title: '정부의 비협조',
        html: `"${disasterToCoin(input2)}" 코인을 가져올 수 없습니다.<br/>
          (도전력 코인으로 막을 수 있어요!)
          `,
      });
    case 2:
      return Swal.fire({
        icon: 'warning',
        title: '시민의식 저하',
        html: `${disasterToCoin(input2)}" 코인을 전부 회수합니다.<br/>
          (공감력 코인으로 막을 수 있어요!)`,
      });
    case 3:
      return Swal.fire({
        icon: 'warning',
        title: '사회적 무관심',
        html: `가져올 수 있는 코인의 개수가 1개로 감소합니다.`,
      });
    case 4:
      return Swal.fire({
        icon: 'warning',
        title: '자원부족',
        html: `전략 카드를 구매할 때 종류당 하나씩 더 코인이 필요합니다.`,
      });
    case 5:
      return Swal.fire({
        icon: 'warning',
        title: '의견충돌',
        html: `갖고있는 직업카드 중 랜덤으로 1장을 잃습니다.<br/>
                  (판단력 코인으로 막을 수 있어요!)`,
      });
    default:
      return;
  }
};

// 코인 출력 함수
export const showCoin = (data: string) => {
  switch (data) {
    case '도전력':
      return <img width="100%" height="100%" src={coinL} alt="" />;
    case '창의력':
      return <img width="100%" height="100%" src={coinC} alt="" />;
    case '판단력':
      return <img width="100%" height="100%" src={coinS} alt="" />;
    case '분석력':
      return <img width="100%" height="100%" src={coinA} alt="" />;
    case '협동력':
      return <img width="100%" height="100%" src={coinM} alt="" />;
    case '공감력':
      return <img width="100%" height="100%" src={coinE} alt="" />;
    default:
      break;
  }
};

// 코인 별 색 출력 함수
export const displayColor = (data: string) => {
  switch (data) {
    case '도전력':
      return '#FFE000';
    case '창의력':
      return '#F4404F';
    case '판단력':
      return '#36D35F';
    case '분석력':
      return '#3B94FF';
    case '협동력':
      return '#3E03A1';
    case '공감력':
      return '#2B4389';
    default:
      break;
  }
};

// 코인 별 능력 출력 함수
export const show = (data: string) => {
  const coinAvailty = [
    {
      name: '도전력',
      num: 3,
      availtyname: '민주적 해결',
      availty: '‘정부 비협조’의 효과(무작위 한 종류 코인 구매불가)를 막습니다.',
    },
    {
      name: '창의력',
      num: 3,
      availtyname: '선한 영향력',
      availty: '이번 라운드에는 코인을 1개씩 추가로 얻습니다.',
    },
    {
      name: '판단력',
      num: 3,
      availtyname: '만장일치',
      availty:
        '‘의견충돌’의 효과(갖고있는 전략카드 중 랜덤으로 1장 회수)를 막습니다.',
    },
    {
      name: '분석력',
      num: 1,
      availtyname: '지식 교환',
      availty: '원하는 코인 1종류를 다른 종류의 코인으로 교환합니다.',
    },
    {
      name: '협동력',
      num: 3,
      availtyname: '토론의 장',
      availty: "'1분'동안 팀과 의논 할 수 있는 시간을 갖습니다.",
    },
    {
      name: '공감력',
      num: 3,
      availtyname: '모범시민',
      availty:
        '시민의식 저하’의 효과(무작위 한 종류 코인을 전부 회수)를 막습니다.',
    },
  ];

  coinAvailty.map(function (item) {
    if (item.name === data) {
      return Swal.fire({
        toast: true,
        title: data,
        html: ` 
          <h2> ${item.availtyname}</h2>
          필요한 코인 갯수 : ${item.num} <br/>
            능 력 : ${item.availty}`,
        width: 500,
        showConfirmButton: true,
        confirmButtonText: '확인',
        background: '#fff',
      });
    }
    return '';
  });
};

