import React, { useContext, useEffect, useState } from 'react';
import './start.css';
import socketService from '../../services/socketService';
import GameContext from '../../gameContext';
import roomService from '../../services/roomService';
import Popup from 'reactjs-popup';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
function Start() {
  const {
    isProtagonist,
    setProtagonist,
    isLogin,
    setLogin,
    isUserNum,
    setUserNum,
    isUserName,
    setUserName,
    isRoomName,
    setRoomName,
  } = useContext(GameContext);
  const history = useNavigate();
  const [openPop, setOpenpop] = useState(false);
  const [option1, setOption1] = useState(false);
  const [option2, setOption2] = useState(false);
  const [option3, setOption3] = useState(false);
  const [socket, setSocket] = useState<any>();

  //  서버 재접속
  useEffect(() => {
    async function reconnect() {
      // 로그인한 기록이 있는가?
      if (window.sessionStorage.login === 'true') {
        // 4명
        if (window.sessionStorage.num === '4') {
          await socketService
            .connect('http://3.38.190.93:7000/')
            .then((rs) => {
              setUserName(window.sessionStorage.id);
              setRoomName(window.sessionStorage.room);
              setUserNum(parseInt(window.sessionStorage.num));
              if (window.sessionStorage.protagonist === 'true') {
                setProtagonist(true);
              } else {
                setProtagonist(false);
              }
              setLogin(true);
              setSocket(rs);
              console.log('Reconnect');
            })
            .catch((err) => {
              console.log('ErrorR: ', err);
            });
        }
        // 5명
        else if (window.sessionStorage.num === '5') {
          await socketService
            .connect('http://3.38.190.93:7000/')
            .then((rs) => {
              setUserName(window.sessionStorage.id);
              setRoomName(window.sessionStorage.room);
              setUserNum(parseInt(window.sessionStorage.num));
              if (window.sessionStorage.protagonist === 'true') {
                setProtagonist(true);
              } else {
                setProtagonist(false);
              }
              setLogin(true);
              setSocket(rs);
              console.log('Reconnect');
            })
            .catch((err) => {
              console.log('ErrorR: ', err);
            });
        }
        // 6명
        else if (window.sessionStorage.num === '6') {
          await socketService
            .connect('http://3.38.190.93:7000/')
            .then((rs) => {
              setUserName(window.sessionStorage.id);
              setRoomName(window.sessionStorage.room);
              setUserNum(parseInt(window.sessionStorage.num));
              if (window.sessionStorage.protagonist === 'true') {
                setProtagonist(true);
              } else {
                setProtagonist(false);
              }
              setLogin(true);
              setSocket(rs);
              console.log('Reconnect');
            })
            .catch((err) => {
              console.log('ErrorR ', err);
            });
        }
      }
    }
    reconnect();
  }, []);

  // Onchange 함수
  const handleOnchange = (e: React.ChangeEvent<any>) => {
    const value = e.target.value;
    switch (e.target.id) {
      // id 입력
      case 'id':
        setUserName(value);
        if (isUserName.length === 5) {
          Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: '이름은 5글자 이상 할수 없습니다!',
          }).then(() => {
            setUserName('');
          });
        }
        break;
      // 방 이름 입력
      case 'room':
        setRoomName(value);
        break;
      // 4명
      case 'option1':
        if (e.target.checked) {
          setOption1(true);
          setOption2(false);
          setOption3(false);
          setUserNum(4);
        } else {
          setOption1(false);
          setOption2(false);
          setOption3(false);
          setUserNum(0);
        }
        break;
      // 5명
      case 'option2':
        if (e.target.checked) {
          setOption1(false);
          setOption2(true);
          setOption3(false);
          setUserNum(5);
        } else {
          setOption1(false);
          setOption2(false);
          setOption3(false);
          setUserNum(0);
        }
        break;
      // 6명
      case 'option3':
        if (e.target.checked) {
          setOption1(false);
          setOption2(false);
          setOption3(true);
          setUserNum(6);
        } else {
          setOption1(false);
          setOption2(false);
          setOption3(false);
          setUserNum(0);
        }
        break;
      // 게임 리더 설정
      case 'protagonist':
        if (e.target.checked) {
          setProtagonist(true);
        } else {
          setProtagonist(false);
        }
        break;
      default:
        break;
    }
  };

  // 서버 접속
  const connectServer = async () => {
    // 4명
    if (
      isUserName !== '' &&
      isRoomName !== '' &&
      isUserNum === 4 &&
      option1 === true &&
      option2 === false &&
      option3 === false
    ) {
      await socketService
        .connect('http://3.38.190.93:7000/')
        .then((rs) => {
          // 게임 리더 로그인 시
          if (isProtagonist) {
            if (socketService.socket) {
              roomService
                .setPlayer(socketService.socket, {
                  user: isUserName,
                  room: isRoomName,
                  boolean: true,
                })
                .then(() => {
                  // 이후 자동 재접속을 위한 windowSession에 정보 저장
                  setLogin(true);
                  setSocket(rs);
                  window.sessionStorage.setItem('id', isUserName);
                  window.sessionStorage.setItem('room', isRoomName);
                  window.sessionStorage.setItem('num', '4');
                  window.sessionStorage.setItem('login', 'true');
                  window.sessionStorage.setItem('protagonist', 'true');
                  console.log(
                    'Connect_server : ' + isUserName + '/' + isRoomName
                  );
                })
                .catch((err) => {
                  alert(err);
                });
            }
          }
          // 일반 유저 로그인 시
          else {
            if (socketService.socket) {
              roomService
                .setPlayer(socketService.socket, {
                  user: isUserName,
                  room: isRoomName,
                  boolean: false,
                })
                .then(() => {
                  // 이후 자동 재접속을 위한 windowSession에 정보 저장
                  setLogin(true);
                  setSocket(rs);
                  window.sessionStorage.setItem('id', isUserName);
                  window.sessionStorage.setItem('room', isRoomName);
                  window.sessionStorage.setItem('num', '4');
                  window.sessionStorage.setItem('login', 'true');
                  window.sessionStorage.setItem('protagonist', 'false');
                  console.log(
                    'Connect_server : ' + isUserName + '/' + isRoomName
                  );
                })
                .catch((err) => {
                  alert(err);
                });
            }
          }
        })
        .catch((err) => {
          console.log('Error: ', err);
        });
    }
    // 5명
    else if (
      isUserName !== '' &&
      isRoomName !== '' &&
      isUserNum === 5 &&
      option1 === false &&
      option2 === true &&
      option3 === false
    ) {
      await socketService
        .connect('http://3.38.190.93:7000/')
        .then((rs) => {
          // 게임 리더 로그인 시
          if (isProtagonist) {
            if (socketService.socket) {
              roomService
                .setPlayer(socketService.socket, {
                  user: isUserName,
                  room: isRoomName,
                  boolean: true,
                })
                .then(() => {
                  // 이후 자동 재접속을 위한 windowSession에 정보 저장
                  setLogin(true);
                  setSocket(rs);
                  window.sessionStorage.setItem('id', isUserName);
                  window.sessionStorage.setItem('room', isRoomName);
                  window.sessionStorage.setItem('num', '5');
                  window.sessionStorage.setItem('login', 'true');
                  window.sessionStorage.setItem('protagonist', 'true');
                  console.log(
                    'Connect_server : ' + isUserName + '/' + isRoomName
                  );
                })
                .catch((err) => {
                  alert(err);
                });
            }
          }
          // 일반 유저 로그인 시
          else {
            if (socketService.socket) {
              roomService
                .setPlayer(socketService.socket, {
                  user: isUserName,
                  room: isRoomName,
                  boolean: false,
                })
                .then(() => {
                  // 이후 자동 재접속을 위한 windowSession에 정보 저장
                  setLogin(true);
                  setSocket(rs);
                  window.sessionStorage.setItem('id', isUserName);
                  window.sessionStorage.setItem('room', isRoomName);
                  window.sessionStorage.setItem('num', '5');
                  window.sessionStorage.setItem('login', 'true');
                  window.sessionStorage.setItem('protagonist', 'false');
                  console.log(
                    'Connect_server : ' + isUserName + '/' + isRoomName
                  );
                })
                .catch((err) => {
                  alert(err);
                });
            }
          }
        })
        .catch((err) => {
          console.log('Error: ', err);
        });
    }
    //6명
    else if (
      isUserName !== '' &&
      isRoomName !== '' &&
      isUserNum === 6 &&
      option1 === false &&
      option2 === false &&
      option3 === true
    ) {
      await socketService
        .connect('http://3.38.190.93:7000/')
        .then((rs) => {
          // 게임 리더 로그인 시
          if (isProtagonist) {
            if (socketService.socket) {
              roomService
                .setPlayer(socketService.socket, {
                  user: isUserName,
                  room: isRoomName,
                  boolean: true,
                })
                .then(() => {
                  // 이후 자동 재접속을 위한 windowSession에 정보 저장
                  setLogin(true);
                  setSocket(rs);
                  window.sessionStorage.setItem('id', isUserName);
                  window.sessionStorage.setItem('room', isRoomName);
                  window.sessionStorage.setItem('num', '6');
                  window.sessionStorage.setItem('login', 'true');
                  window.sessionStorage.setItem('protagonist', 'true');
                  console.log(
                    'Connect_server  : ' + isUserName + '/' + isRoomName
                  );
                })
                .catch((err) => {
                  alert(err);
                });
            }
          }
          // 일반 로그인 시
          else {
            if (socketService.socket) {
              roomService
                .setPlayer(socketService.socket, {
                  user: isUserName,
                  room: isRoomName,
                  boolean: false,
                })
                .then(() => {
                  // 이후 자동 재접속을 위한 windowSession에 정보 저장
                  setLogin(true);
                  setSocket(rs);
                  window.sessionStorage.setItem('id', isUserName);
                  window.sessionStorage.setItem('room', isRoomName);
                  window.sessionStorage.setItem('num', '6');
                  window.sessionStorage.setItem('login', 'true');
                  window.sessionStorage.setItem('protagonist', 'false');
                  console.log(
                    'Connect_server : ' + isUserName + '/' + isRoomName
                  );
                })
                .catch((err) => {
                  alert(err);
                });
            }
          }
        })
        .catch((err) => {
          console.log('Error: ', err);
        });
    } else {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: '서버에 연결 할 수 없습니다.',
      });
    }
  };

  // 버튼 동작 함수
  const transPage = async (e: React.ChangeEvent<any>) => {
    switch (e.target.id) {
      // 툴킷 이동
      case 'toolkit':
        await roomService
          .joinRoom(socket, isRoomName, isUserName, isUserNum, e.target.id)
          .then((rs) => {
            // 사전 툴킷
            if (rs) history('/ToolkitBefore');
            // 사후 툴킷
            else history('/ToolkitAfter');
          })
          .catch((err) => {
            alert(err);
          });
        break;
      // 게임 이동
      case 'game':
        await roomService
          .joinRoom(socket, isRoomName, isUserName, isUserNum, e.target.id)
          .then(() => {
            history('/Game');
          })
          .catch((err) => {
            alert(err);
          });
        break;
      // 만족도 조사 이동
      case 'csat':
        history('/CSAT');
        break;
      // 랭킹 보드 이동
      case 'board':
        history('/Board');
        break;
      default:
        break;
    }
  };

  // 로그아웃 함수
  const disconnectServer = async () => {
    if (window.sessionStorage.login === 'true') {
      // 서버와 연결 끊기
      await roomService
        .disconnect(socket, isUserName, isRoomName)
        .then(() => {
          Swal.fire({
            icon: 'success',
            title: 'LogOut',
            text: '로그아웃 되었습니다.',
          }).then(() => {
            // local 변수 & windowSession 정보 clear
            setLogin(false);
            setUserName('');
            setRoomName('');
            setUserNum(0);
            setProtagonist(false);
            setOption1(false);
            setOption2(false);
            setOption3(false);
            window.sessionStorage.clear();
            console.log('disconnect');
          });
        })
        .catch((error) => {
          alert(error);
        });
    }
  };

  return (
    <div className="Startpage">
      <div className="Title1"></div>
      <div className="Title2"></div>

      {isLogin ? (
        <div className="Login">
          {isProtagonist ? (
            <div className="Leadertitle">
              {isUserName}({isRoomName}/{isUserNum})
              <div className="Logoutbtn" onClick={disconnectServer}>
                X
              </div>
            </div>
          ) : (
            <div className="Usertitle">
              {isUserName}({isRoomName}/{isUserNum})
              <div className="Logoutbtn" onClick={disconnectServer}>
                X
              </div>
            </div>
          )}

          <div className="Controllebutton2" id="toolkit" onClick={transPage}>
            툴킷 작성
          </div>

          <Popup
            trigger={
              <div
                className="Controllebutton1"
                onClick={() => {
                  setOpenpop(true);
                }}
              >
                게임 하기
              </div>
            }
            open={openPop}
            modal
            nested
          >
            <div className="Alertpop">
              <div className="Controllebutton1" id="game" onClick={transPage}>
                게임 시작
              </div>
              <div className="Controllebutton2" id="board" onClick={transPage}>
                랭킹 확인
              </div>
            </div>
          </Popup>

          <div className="Controllebutton2" id="csat" onClick={transPage}>
            만족도 조사
          </div>
        </div>
      ) : (
        <div className="Login">
          <div className="CheckBoxFlex">
            <div>
              <input
                className="CheckBox"
                type="checkbox"
                id="option1"
                checked={option1}
                onChange={handleOnchange}
              />
              4 players
            </div>
            <div>
              <input
                className="CheckBox"
                type="checkbox"
                id="option2"
                checked={option2}
                onChange={handleOnchange}
              />
              5 players
            </div>
            <div>
              <input
                className="CheckBox"
                type="checkbox"
                id="option3"
                checked={option3}
                onChange={handleOnchange}
              />
              6 players
            </div>
          </div>
          <div className="CheckBoxFlex2">
            <div>
              <input
                className="CheckBox"
                type="checkbox"
                id="protagonist"
                checked={isProtagonist}
                onChange={handleOnchange}
              />
              Game Leader
            </div>
          </div>
          <input
            className="Logininput"
            placeholder="Room ID"
            id="room"
            value={isRoomName}
            onChange={handleOnchange}
          />

          <input
            className="Logininput"
            id="id"
            placeholder="User ID"
            value={isUserName}
            onChange={handleOnchange}
          />

          <button className="Loginbtn" onClick={() => connectServer()}>
            로그인
          </button>
        </div>
      )}
    </div>
  );
}

export default Start;
