import { Socket } from "socket.io-client";
import {IStartGame } from "../../components/game";
class GameService {

  //버튼 콜백 함수
  public async callback(
    socket: Socket,
    listiner: (options:{callback:string, data : any})=>void
  )
  {
    socket.on('callback', listiner)
  }
  // 게임 시작시 입장인원 받아오기
  public async EnterGame
  (socket: Socket,listiner: (options: IStartGame) => void) {
    socket.on("enter_game", listiner);
  }

  // 게임 시작 시 or 게임 재접속 시 게임 데이터 받아오는 함수
  public async onStartGame(socket: Socket,listiner: (options: IStartGame) => void) {
    socket.on("start_game", listiner);
  }

  // 게임 시작 시 점수 계산을 위한 게임 시작시간 보내기
  public async setGameStart
  (socket: Socket,data: any){
    socket.emit('set_GameStart',{data})
  }

  // 각 턴 끝나면 보내는 함수
  public async endTurn(socket: Socket, data: any) {
    console.log('emitting end_Turn')
    socket.emit("end_Turn", {data});
  }

  // Availty turn, 능력 사용 시 
  public async selectAvailty(socket: Socket, data: any) {
    socket.emit("select_Availty", {data});
  }

  // Buy turn, 카드 구매시
  public async SelectCard(socket: Socket, data:any) {
    socket.emit("select_Card", {data});
  }

  //첫번째 phase (코인 5개 사기) 
  public async receivePhaseFirstTurn(socket: Socket,listiner: (options: IStartGame) => void){
    socket.on("phaseFirst", listiner);
  }

  // Availty phase
  public async receivePhaseAvailty(socket: Socket,listiner: (options: IStartGame) => void){
    socket.on("phaseAvailty", listiner);
  }

  // Buy phase
  public async receivePhaseBuy(socket: Socket,listiner: (options: IStartGame) => void){
    socket.on("phaseBuy", listiner);
  }

  //State phase
  public async receivePhaseState(socket: Socket,listiner: (options: IStartGame) => void){
    socket.on("phaseState", listiner);
  }

  // NextRound
  public async receivePhaseNextRound(socket: Socket,listiner: (options: IStartGame) => void){
    socket.on("phaseNextRound", listiner);
  }

  //각 phase가 끝나고 phase 변화요청을 위한 함수
  public async phaseChange(socket:Socket,data:any){
    socket.emit('phase_Change', {data})
  }

  //3라운드 마다 오는 장애물을 받아오는 함수
  public async onDisaster(socket: Socket,listiner: (options: IStartGame) => void) {
    socket.on("On_disaster", listiner);
  }

  //게임 종료를 알리는 함수
  public async endGame(socket: Socket, roomID: String, playerID: Array<String>, round: Number,protagonist:Boolean){
    socket.emit("end_Game", {roomID,playerID,round,protagonist})}


 

 








 

}

export default new GameService();
