import gameContext from '../../../gameContext';
import { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import socketService from '../../../services/socketService';
import toolkitService from '../../../services/toolkitService';
import Swal from 'sweetalert2';
import { goodValueContext, badValueContext } from '../../../gameContext';

export interface IStartToolkit {
  enter: number;
  protagonist: string;
  phase: number;
  townName: string;
  villageProblem: string;
  goodValue: number[];
  occupation: string[];
  win: number[];
}

export function Toolkitbefore() {
  const {
    isUserNum,
    isUserName,
    isRoomName,
    isToolkitArray,
    setToolkitArray,
    isToolkitArray2,
    setToolkitArray2,
    isToolkitJoblist,
    setToolkitJoblist,
    Village,
    setVillage,
    VillagProblem,
    setVillageProblem,
    isWin,
    setWin,
    isToolkitphase,
    setToolkitphase,
    isProtagonist,
    setProtagonist,
  } = useContext(gameContext);

  const [rendering, setRendring] = useState<boolean>(true);
  const history = useNavigate();
  const [access, setAccess] = useState(false);
  const [str, setStr] = useState('');
  const [Enter, setEnter] = useState<number>(0);
  const [newArry, setNewArry] = useState([]);
  const [newArry2, setNewArry2] = useState([]);

  // 사전 툴킷 입장
  const handleToolkitEnter = () => {
    if (socketService.socket)
      toolkitService.EnterToolkit(socketService.socket, async (option) => {
        setEnter(option.enter);
      });
  };

  // 사전 툴킷 첫 접속 & 재접속시 정보 받아오기
  const handleToolkitStart = () => {
    if (socketService.socket) {
      toolkitService.onStartToolkit(socketService.socket, async (options) => {
        // protagonist 설정
        if (options.protagonist === isUserName && options.phase === 0) {
          setProtagonist(true);
          if (socketService.socket) {
            toolkitService.updateStartToolkit(socketService.socket, {
              id: isRoomName,
              phase: 1,
            });
          }
          console.log('Start');
        }
        console.log(options.phase);
        setToolkitphase(options.phase);
        if (options.goodValue.length !== 0) {
          let a: any = Array<string>(6);
          let b: any = Array<string>(6);
          for (let i = 0; i < options.goodValue.length; i++) {
            a[i] = goodValueContext[options.goodValue[i]];
            b[i] = badValueContext[options.goodValue[i]];
            if (i === options.goodValue.length - 1) {
              setNewArry(a);
              setNewArry2(b);
            }
          }
        }

        setToolkitJoblist(options.occupation);
        setVillage(options.townName);
        setVillageProblem(options.villageProblem);

        setWin([
          { name: '신제품개발', num: options.win[0] },
          { name: '지자체공공기관', num: options.win[1] },
          { name: '공유플랫폼', num: options.win[2] },
          { name: '공동체/인적자원', num: options.win[3] },
          { name: '디자인', num: options.win[4] },
          { name: '지역자원활용', num: options.win[5] },
        ]);
      });
    }
  };

  // 사전 툴킷 phase 변화를 위해 서버에 요청 하는 함수
  const phaseChange = () => {
    if (isProtagonist === true) {
      if (socketService.socket) {
        switch (isToolkitphase) {
          case 1:
            // let indexArray: Array<string> = [];
            // isToolkitArray.map((item, index) => {
            //   if (item.act === true) {
            //     indexArray = indexArray.concat(index);
            //   }
            //   return '';
            // });
            let indexArray: Array<Number> = [];
            for (let i = 0; i < 27; i++) {
              if (isToolkitArray[i] === true) {
                indexArray = indexArray.concat(i);
              }
              if (i === 26)
                toolkitService.phaseChange(socketService.socket, {
                  phase: isToolkitphase + 1,
                  goodValue: indexArray,
                  id: isRoomName,
                });
            }

            break;
          case 2:
            toolkitService.phaseChange(socketService.socket, {
              phase: isToolkitphase + 1,
              townName: Village,
              villageProblem: VillagProblem,
              occupation: isToolkitJoblist,
              id: isRoomName,
            });
            break;
          default:
            break;
        }
      }
    }
  };

  const selectContent = (i: number, check: boolean) => {
    if (isProtagonist === true) {
      if (socketService.socket) {
        toolkitService.selectContent(
          socketService.socket,
          isToolkitArray,
          isToolkitArray2,
          i,
          check
        );
      }
    }
  };

  // 사전 툴킷 phase 1 활성화된 마을 특징 갯수 세기 (6개)
  const countActive = (array: Array<any>) => {
    return array.filter((array) => array === true).length;
  };

  // Onchange 함수
  const handleOnchange = (e: React.ChangeEvent<any>) => {
    setRendring(false);
    const value = e.target.value;
    switch (e.target.id) {
      case 'Village':
        setVillage(value);
        break;
      case 'VillagProblem':
        setVillageProblem(value);
        break;
      case 'isToolkitJoblist':
        setStr(value);
        break;
      default:
        break;
    }
  };

  // 사전 툴킷 phase 2 마을 정보 서버에 보내기
  const onChangeWrite = (id: string, contents: string) => {
    if (isProtagonist) {
      if (socketService.socket) {
        toolkitService.onChangeWrite(socketService.socket, id, contents);
        setRendring(true);
        setStr('');
      }
    }
  };

  // 사전 툴킷 phase 3 승리 조건 서버에 보내기
  const onChangeWin = (array: Array<any>) => {
    let sum = 0;
    array.map((item) => (sum = sum + item.num));
    console.log(sum);
    if (sum === 10) {
      setAccess(true);
      if (isProtagonist) {
        if (socketService.socket) {
          toolkitService.onChangeWin(socketService.socket, array);
        }
      }
      Swal.fire({
        icon: 'success',
        title: 'Wow',
        text: '승리 조건 완성!',
      });
    } else {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: '선택된 직업카드의 수가 10개여야 합니다!',
      });
    }
  };

  // 사전 툴킷 phase 3 승리 조건 설정 버튼 제어 함수
  const plusminus = (i: number, option: string) => {
    setRendring(false);
    if (isProtagonist) {
      if (option === '+') {
        let newArray = isWin.map((item, index) =>
          index === i ? { name: item.name, num: item.num + 1 } : item
        );
        setWin(newArray);
      } else {
        let newArray = isWin.map((item, index) =>
          index === i
            ? item.num <= 0
              ? item
              : { name: item.name, num: item.num - 1 }
            : item
        );
        setWin(newArray);
      }
    }
  };

  // 사전 툴킷 종료 함수
  const finishToolkit = () => {
    // 모든 이용자가 체크 표시를 눌러 승리조건 적용 필요
    if (access) {
      if (socketService.socket)
        toolkitService.finishToolkit(socketService.socket, {
          roomName: isRoomName,
          userName: isUserName,
          before: true,
          isWin: isWin,
        });
      console.log(isWin);
      history('/');
    } else {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: '오른쪽 위의 체크 표시를 먼저 눌러주세요!',
      });
    }
  };

  // 툴킷 시작 & 툴킷 정보 & phase 변화를 서버에서 받아오는 함수
  useEffect(() => {
    function change() {
      if (socketService.socket) {
        toolkitService.phaseChanger(socketService.socket, (phase) => {
          if (socketService.socket) {
            setToolkitphase(phase);
            console.log('next Phase');
          }
        });
      }
    }
    function check() {
      switch (isToolkitphase) {
        case 1:
          if (socketService.socket) {
            toolkitService.updateContent(socketService.socket, (data) => {
              console.log(data);
              let newArray = data.contents;
              let newArray2 = data.contents2;

              if (data.check) {
                newArray[data.number] = true;
                newArray2[data.number] = true;
              } else {
                newArray[data.number] = false;
                newArray2[data.number] = false;
              }

              setToolkitArray(newArray);
              setToolkitArray2(newArray2);
            });
          }
          break;
        case 2:
          if (socketService.socket) {
            toolkitService.updateWrite(socketService.socket, (id, contents) => {
              switch (id) {
                case 'Village':
                  setVillage(contents);
                  break;
                case 'VillagProblem':
                  setVillageProblem(contents);
                  break;
                case 'isToolkitJoblist':
                  setToolkitJoblist(isToolkitJoblist.concat(contents + ', '));
                  break;
                default:
                  break;
              }
            });
          }
          break;
        case 3:
          if (socketService.socket) {
            toolkitService.updateWin(socketService.socket, (i) => {
              let sum = 0;
              i.map((item) => (sum = sum + item.num));
              if (sum === 10) setWin(i);
            });
          }
          break;
        default:
          break;
      }
    }
    if (rendering) {
      change();
      check();
    }
    if (Enter < parseInt(window.sessionStorage.num)) {
      handleToolkitEnter();
      handleToolkitStart();
    }
  });

  // 마을 특징 고르기 제어 함수
  function UpdateContent({
    choice,
    txt,
    index,
  }: {
    choice: boolean;
    txt: any;
    index: number;
  }) {
    if (choice) {
      return (
        <div
          className="ToolkitpageGridbtnA"
          key={index}
          onClick={() => selectContent(index, false)}
        >
          {txt}
        </div>
      );
    } else {
      return (
        <div
          className="ToolkitpageGridbtn"
          key={index}
          onClick={() => selectContent(index, true)}
        >
          {txt}
        </div>
      );
    }
  }

  const popupdisplay = (i: number) => {
    switch (i) {
      case 0:
        return Swal.fire({
          title: '신제품 개발',
          html: `
          1. 분리수거를 귀찮아 하는 사람들, 쓰레기 무단투기를 방지하기 위해 자체적으로 재활용이 되는 쓰레기통 개발 <br/>
          2. 코로나 19로 인한 어린이의 안전이 염려된다! 어린이 전용 그네에 자동으로 소독되는 자동소독장치 개발!  <br/>
          `,
        });
      case 1:
        return Swal.fire({
          title: '지자체 공공기관',
          html: `
          1. 나도 저거 하고 싶다! 구청, 시청에서 청소년 사회참여 프로그램을 만들어달라! 청소년들의 참여활동 증가!<br/>
          2. 한 번쓰고 버리지말고 아껴쓰고 나눠쓰고 바꿔쓰고 다시 쓰자! 마을축제를 열어 아나바다를 열어보자. <br/>
          `,
        });
      case 2:
        return Swal.fire({
          title: '공유플랫폼',
          html: `
          1. 전동킥보드 주차문제로 청소년들이 모여 캠페인을 열고자 한다! 전국 청소년 오픈채팅방을 개설하여 캠패인 인원 모집.  <br/>
          2. 우리동네 안전구역을 찾아 SNS숏츠 영상 제작을 통해 범죄예방 챌린지 진행!  <br/>
          
          `,
        });
      case 3:
        return Swal.fire({
          title: '공동체/인적자원',
          html: `
          1. 우리 할머니 글자를 알려드리고 싶은데… 마을공동체 활성화를 통한 사회적 약자를 위한 교육 진행. <br/>
          2. 쓰레기가 너무 많이 버려지고 있어, 함께 할 사람들을 모아 공동체로 줍깅 프로젝트를 활성화 시켜보자. <br/>
          `,
        });
      case 4:
        return Swal.fire({
          title: '디자인',
          html: `
          1. 시각장애가 있는 분들이 음식점에서 음식을 시켜먹기 어려워 한다고?! 시각장애를 위한 점자 메뉴판을 디자인해보자. <br/>
          2. 바닥 대신에 담배곽에 꽁초를 버릴 수 있는 담배곽을 디자인 해볼까? <br/>
          `,
        });
      case 5:
        return Swal.fire({
          title: '지역자원활용',
          html: `
          1. 몇 년동안 아무도 안쓰는 공간, 버려진 유휴공간을 활용하여 공유 주방으로! 마을 사람들이 모여 함께 요리 해보자. <br/>
          2. 먼지가 날리는 공사판이 아닌, 역사를 배울 수 있는 공사판 외벽을 활용한 마을 역사 전시를 열어보자.  <br/>
          
          `,
        });

      default:
        break;
    }
  };

  return (
    <div className="Toolkitpage">
      {Enter === parseInt(window.sessionStorage.num) ? (
        isToolkitphase === 1 ? (
          <>
            <div className="Toolkittitle">
              우리 마을의 특징 골라보기 {isProtagonist}
              {countActive(isToolkitArray) === 6 ? (
                <div
                  className="Toolkitpagebtn"
                  style={{ position: 'absolute', left: '1200px' }}
                  onClick={() => phaseChange()}
                />
              ) : (
                ''
              )}
            </div>

            <div className="ToolkitpageGrid">
              {isToolkitArray.map((item, index) => (
                <UpdateContent
                  choice={item}
                  txt={goodValueContext[index]}
                  index={index}
                  key={index}
                ></UpdateContent>
              ))}
            </div>
          </>
        ) : isToolkitphase === 2 ? (
          <>
            <div className="Toolkittitle">
              우리 마을을 소개합니다.
              {isToolkitJoblist.length > 6 ? (
                <div
                  className="Toolkitpagebtn"
                  style={{ position: 'absolute', left: '1200px' }}
                  onClick={() => phaseChange()}
                />
              ) : (
                ''
              )}
            </div>
            <div className="ToolkitpageText">
              마을이름 :
              <input
                className="ToolkitpageInput"
                id="Village"
                value={Village}
                type="text"
                onChange={handleOnchange}
              />
              <div
                className="Toolkitpagebtn"
                onClick={() => onChangeWrite('Village', Village)}
              />
            </div>
            <div className="ToolkitpageText">마을특징 :</div>
            <div className="ToolkitpageGridS">
              {newArry.length !== 0
                ? newArry.map((item, index) => {
                    return (
                      <div className="ToolkitpageGridbtn" key={index}>
                        {item}
                      </div>
                    );
                  })
                : isToolkitArray.map((item, index) => {
                    if (item)
                      return (
                        <div className="ToolkitpageGridbtn" key={index}>
                          {goodValueContext[index]}
                        </div>
                      );
                    return null;
                  })}
            </div>
            <div className="ToolkitpageGridS">
              {newArry2.length !== 0
                ? newArry2.map((item, index) => {
                    return (
                      <div className="ToolkitpageGridbtnR" key={index}>
                        {item}
                      </div>
                    );
                  })
                : isToolkitArray2.map((item, index) => {
                    if (item)
                      return (
                        <div className="ToolkitpageGridbtnR" key={index}>
                          {badValueContext[index]}
                        </div>
                      );
                    return null;
                  })}
            </div>
            <div className="ToolkitpageText">
              해결 할 마을문제 :
              <select
                className="ToolkitpageInput"
                id="VillagProblem"
                value={VillagProblem}
                onChange={handleOnchange}
              >
                <option className="ToolkitpageInput">선택하기</option>
                <option className="ToolkitpageInput">쓰레기 문제</option>
                <option className="ToolkitpageInput">치안 / 안전</option>
                <option className="ToolkitpageInput">노동 / 고용</option>
                <option className="ToolkitpageInput">도시재생</option>
                <option className="ToolkitpageInput">노인 / 소외계층 </option>
              </select>
              <div
                className="Toolkitpagebtn"
                onClick={() => onChangeWrite('VillagProblem', VillagProblem)}
              />
            </div>
            <div className="ToolkitpageText">
              문제해결 방법 :
              <input
                className="ToolkitpageInput"
                id="isToolkitJoblist"
                value={str}
                type="text"
                onChange={handleOnchange}
              />
              <div
                className="Toolkitpagebtn"
                style={{ margin: '0 20px' }}
                onClick={() => onChangeWrite('isToolkitJoblist', str)}
              />
            </div>
            <div className="ToolkitpageTextArea">{isToolkitJoblist}</div>
          </>
        ) : isToolkitphase === 3 ? (
          <>
            <div className="Toolkittitle">
              우리 마을을 소개합니다.
              <div
                className="Toolkitpagebtn"
                style={{ position: 'absolute', left: '1200px' }}
                onClick={() => onChangeWin(isWin)}
              />
            </div>
            <div className="ToolkitpageText">
              문제해결 방법 : {isToolkitJoblist}
            </div>
            <div className="ToolkitpageGridG">
              <div className="ToolkitpageGridbtnG0">
                <div
                  className="ToolkitpageGridbtnGCbtn"
                  onClick={() => popupdisplay(0)}
                />
                <div className="ToolkitpageGridbtnGlabel">
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(0, '+');
                    }}
                  />
                  {isWin[0].num}
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(0, '-');
                    }}
                  />
                </div>
              </div>
              <div className="ToolkitpageGridbtnG1">
                <div
                  className="ToolkitpageGridbtnGCbtn"
                  onClick={() => popupdisplay(1)}
                />
                <div className="ToolkitpageGridbtnGlabel">
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(1, '+');
                    }}
                  />
                  {isWin[1].num}
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(1, '-');
                    }}
                  />
                </div>
              </div>
              <div className="ToolkitpageGridbtnG2">
                <div
                  className="ToolkitpageGridbtnGCbtn"
                  onClick={() => popupdisplay(2)}
                />
                <div className="ToolkitpageGridbtnGlabel">
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(2, '+');
                    }}
                  />
                  {isWin[2].num}
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(2, '-');
                    }}
                  />
                </div>
              </div>
              <div className="ToolkitpageGridbtnG3">
                <div
                  className="ToolkitpageGridbtnGCbtn"
                  onClick={() => popupdisplay(3)}
                />
                <div className="ToolkitpageGridbtnGlabel">
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(3, '+');
                    }}
                  />
                  {isWin[3].num}
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(3, '-');
                    }}
                  />
                </div>
              </div>
              <div className="ToolkitpageGridbtnG4">
                <div
                  className="ToolkitpageGridbtnGCbtn"
                  onClick={() => popupdisplay(4)}
                />
                <div className="ToolkitpageGridbtnGlabel">
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(4, '+');
                    }}
                  />
                  {isWin[4].num}
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(4, '-');
                    }}
                  />
                </div>
              </div>
              <div className="ToolkitpageGridbtnG5">
                <div
                  className="ToolkitpageGridbtnGCbtn"
                  onClick={() => popupdisplay(5)}
                />
                <div className="ToolkitpageGridbtnGlabel">
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(5, '+');
                    }}
                  />
                  {isWin[5].num}
                  <div
                    className="ToolkitpageGridbtnGlabelbtn"
                    onClick={() => {
                      plusminus(5, '-');
                    }}
                  />
                </div>
              </div>
            </div>
            <div className="ToolkitpageText2">
              규칙1. 코인은 1라운드에 2개씩 모을 수 있습니다.(첫라운드만 5개)
            </div>
            <div className="ToolkitpageText2">
              규칙2. 코인을 모아서, 전략카드를 구매하거나 코인별로 기능을 사용할
              수 있습니다.
              <div
                className="Toolkitpagehomebtn"
                onClick={() => finishToolkit()}
              />
            </div>
            <div className="ToolkitpageText2">
              규칙3. 3턴마다 장애물이 있습니다. 장애물은 코인을 사용하여 피해갈
              수 있습니다.
            </div>
          </>
        ) : (
          <>
            <div className="Toolkittitle">
              우리 마을의 특징 골라보기 {isProtagonist}
              {countActive(isToolkitArray) === 6 ? (
                <div
                  className="Toolkitpagebtn"
                  style={{ position: 'absolute', left: '1200px' }}
                  onClick={() => phaseChange()}
                />
              ) : (
                ''
              )}
            </div>

            <div className="ToolkitpageGrid">
              {isToolkitArray.map((item, index) => (
                <UpdateContent
                  choice={item}
                  txt={goodValueContext[index]}
                  index={index}
                  key={index}
                ></UpdateContent>
              ))}
            </div>
          </>
        )
      ) : (
        <>
          <div className="Wait_Logo_P" />
          <div className="Wait_Logo_L" />
          <div className="Wait_Logo_A" />
          <div className="Wait_Logo_N" />
          <div className="Wait_Logo_V" />
          <div className="Wait_Logo_I" />
          <div className="Wait_Logo_L2" />
          <div className="Wait_Logo_L3" />
          <div className="Wait_Logo_A2" />
          <div className="Wait_Logo_G" />
          <div className="Wait_Logo_E" />
          <div className="Wait_User">
            {isRoomName === '' ? (
              <div className="reStartbutton" onClick={() => history('/')}>
                재접속
              </div>
            ) : (
              <>
                방 제목: {isRoomName} <br />
              </>
            )}
            <br />
            입장인원:{' '}
            {Enter !== 0
              ? `${Enter}/${isUserNum}`
              : `${isUserNum}/${isUserNum}`}
          </div>
        </>
      )}
      <button
        onClick={() => {
          console.log(isToolkitArray);
        }}
      ></button>
    </div>
  );
}
