import gameContext from '../../../gameContext';
import { useContext } from 'react';
import { goodValueContext } from '../../../gameContext';

export function Toolkit() {
  const {
    isToolkitArray,
    displayToolkit,
    isRoomName,
    Village,
    VillagProblem,
    isWin,
    isToolkitJoblist,
    textarea1,
    textarea2,
    textarea3,
    isScore,
    allUserName,
  } = useContext(gameContext);

  let d: any[] = [];
  let t1: any[] = [];
  let t2: any[] = [];
  let t3: any[] = [];

  // 마을 특징 출력 함수
  const displayD = () => {
    for (let i = 0; i < isToolkitArray.length; i++) {
      for (let j = 0; j < displayToolkit.length; j++) {
        console.log(i === displayToolkit[j]);
        if (i === displayToolkit[j]) {
          d.push(
            <div className="ToolkitpageGridbtn" key={i}>
              {goodValueContext[i]}
            </div>
          );
        }
      }
    }
    return d;
  };

  // 사후 툴킷 내용 출력 함수
  const display = (array: Array<string>) => {
    if (array.length === 0) return;
    else {
      switch (array) {
        case textarea1:
          for (var i = 0; i < array.length; i++) {
            t1.push(
              <>
                {array[i]}
                <br />
              </>
            );
          }
          return t1;
        case textarea2:
          for (var j = 0; j < array.length; j++) {
            t2.push(
              <>
                {array[j]}
                <br />
              </>
            );
          }
          return t2;
        case textarea3:
          for (var k = 0; k < array.length; k++) {
            t3.push(
              <>
                {array[k]}
                <br />
              </>
            );
          }
          return t3;
        default:
          break;
      }
    }
  };

  return (
    <div className="Toolkitfinishpage">
      <div className="Tips" style={{ margin: '20px auto' }}>
        우리가 마을문제를 해결하기 위한 노력들
      </div>
      <div className="ToolkitfinishpageText">
        학교 / 반 :<div className="ToolkitfinishpageInput">{isRoomName}</div>
      </div>
      <div className="ToolkitfinishpageText">
        마을이름 :<div className="ToolkitfinishpageInput">{Village}</div>
      </div>
      <div className="ToolkitfinishpageText">
        조 원 :
        <div className="ToolkitfinishpageInput">
          {allUserName.map((item: any) => {
            return ' ' + item + '';
          })}
        </div>
      </div>

      <div className="Toolkitfinishpageline" />

      <div className="ToolkitfinishpageText">마 을 특 징 :</div>
      <div className="ToolkitpageGridS">{displayD()}</div>

      <div className="ToolkitfinishpageText">
        해결 할 마을 문제 :
        <div className="ToolkitfinishpageInput">{VillagProblem}</div>
      </div>
      <div className="ToolkitfinishpageText">
        필요한 직업 :
        <div className="ToolkitfinishpageInput">{isToolkitJoblist}</div>
      </div>
      <div className="ToolkitfinishpageGridG_S">
        <div className="ToolkitfinishpageGridbtnG0">
          <div className="ToolkitfinishpageGridbtnGlabel">
            <div className="ToolkitpageGridbtnGlabelbtn" />
            {isWin[0]}
            <div className="ToolkitpageGridbtnGlabelbtn" />
          </div>
        </div>
        <div className="ToolkitfinishpageGridbtnG1">
          <div className="ToolkitfinishpageGridbtnGlabel">
            <div className="ToolkitpageGridbtnGlabelbtn" />
            {isWin[1]}
            <div className="ToolkitpageGridbtnGlabelbtn" />
          </div>
        </div>
        <div className="ToolkitfinishpageGridbtnG2">
          <div className="ToolkitfinishpageGridbtnGlabel">
            <div className="ToolkitpageGridbtnGlabelbtn" />
            {isWin[2]}
            <div className="ToolkitpageGridbtnGlabelbtn" />
          </div>
        </div>
        <div className="ToolkitfinishpageGridbtnG3">
          <div className="ToolkitfinishpageGridbtnGlabel">
            <div className="ToolkitpageGridbtnGlabelbtn" />
            {isWin[3]}
            <div className="ToolkitpageGridbtnGlabelbtn" />
          </div>
        </div>
        <div className="ToolkitfinishpageGridbtnG4">
          <div className="ToolkitfinishpageGridbtnGlabel">
            <div className="ToolkitpageGridbtnGlabelbtn" />
            {isWin[4]}
            <div className="ToolkitpageGridbtnGlabelbtn" />
          </div>
        </div>
        <div className="ToolkitfinishpageGridbtnG5">
          <div className="ToolkitfinishpageGridbtnGlabel">
            <div className="ToolkitpageGridbtnGlabelbtn" />
            {isWin[5]}
            <div className="ToolkitpageGridbtnGlabelbtn" />
          </div>
        </div>
      </div>
      <div className="ToolkitfinishpageText">
        게 임 점 수 :
        <div className="ToolkitfinishpageInput">{isScore + '점'}</div>
      </div>

      <div className="Toolkitfinishpageline" />
      <div className="ToolkitAfterpageGrid">
        <div className="ToolkitAfterpageGridS">
          <div>
            <div className="ToolkitAfterpageText">
              Q. 사회문제를 해결하는데에 각 직업의 역할은 무엇이었나요?
            </div>
            <div className="ToolkitpageAfterTextArea1">
              {display(textarea1)}
            </div>
          </div>
        </div>
        <div className="ToolkitAfterpageGridS">
          <div>
            <div className="ToolkitAfterpageText">
              Q. 플랜빌리지 게임 진행 시, 나만의 전략이 있었나요?
            </div>
            <div className="ToolkitpageAfterTextArea2">
              {display(textarea2)}
            </div>
          </div>
          <div>
            <div className="ToolkitAfterpageText">
              Q. 게임진행 시, 어려웠던 점은 무엇이었나요?
            </div>
            <div className="ToolkitpageAfterTextArea2">
              {display(textarea3)}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
