import { Socket } from 'socket.io-client';
import { IStartToolkit } from '../../components/toolkit/before';
class ToolkitService {
  // 툴킷 입장 시 입장인원 받아오기
  public async EnterToolkit(
    socket: Socket,
    listiner: (options: IStartToolkit) => void
  ) {
    socket.on('enter_toolkit', listiner);
  }

  // 툴킷 시작 시 or 툴킷 재접속 시 툴킷 데이터 받아오는 함수
  public async onStartToolkit(
    socket: Socket,
    listiner: (options: IStartToolkit) => void
  ) {
    socket.on('start_toolkit', listiner);
  }

  public async updateStartToolkit(socket: Socket, data: any) {
    socket.emit('update_start_toolkit', data);
  }

  // phase가 끝났다고 알리는 함수
  public async phaseChange(socket: Socket, data: any) {
    socket.emit('Toolkitphase_Change', data);
  }

  // phase를 바꾸는 함수
  public async phaseChanger(socket: Socket, listiner: (phase: any) => void) {
    socket.on('Toolkitphase_Changed', listiner);
  }

  public async selectContent(
    socket: Socket,
    contents: any,
    contents2: any,
    number: Number,
    check: boolean
  ) {
    socket.emit('select_contents', { contents, contents2, number, check });
  }

  // 툴킷 1 phase 시 특징 받는 함수 (All)
  public async updateContent(
    socket: Socket,
    listiner: (contents: any) => void
  ) {
    socket.on('update_contents', listiner);
  }

  // 툴킷 2,4 phase 시 특징 보내는 함수 (Protagonist)
  public async onChangeWrite(socket: Socket, id: string, contents: string) {
    socket.emit('onchange_write', { id, contents });
  }

  // 툴킷 2,4 phase 시 특징 받는 함수 (All)
  public async updateWrite(
    socket: Socket,
    listiner: (id: string, contents: string) => void
  ) {
    socket.on('update_write', ({ id, contents }) => listiner(id, contents));
  }

  // 툴킷 3 phase 시 승리조건 보내는 함수 (Protagonist)
  public async onChangeWin(socket: Socket, win: Array<any>) {
    socket.emit('onchange_win', win);
  }

  // 툴킷 3 phase 시 승리조건 받는 함수 (All)
  public async updateWin(socket: Socket, listiner: (win: Array<any>) => void) {
    socket.on('update_win', listiner);
  }

  // 툴킷 작성 종료 결과 보내는 함수
  public async finishToolkit(socket: Socket, data: any): Promise<boolean> {
    return new Promise((rs, rj) => {
      socket.emit('finish_toolkit', { data }, () => rs(true));
    });
  }
}

export default new ToolkitService();
