import '../../App.css';
import React, { useEffect, useState } from 'react';
import socketService from '../../services/socketService';
import otherService from '../../services/otherService';
import { useNavigate } from 'react-router-dom';

export function Board() {
  const [info, setInfo] = useState<any>([]);
  const history = useNavigate();

  useEffect(() => {
    // 랭킹 보드 사전 작업
    function handleDatabase() {
      if (socketService.socket) {
        // 랭킹보드 데이터 요청
        otherService.callBoard(socketService.socket, '');
        console.log('calling Data...');
      }
      if (socketService.socket) {
        // 랭킹 보드 데이터 받아서 적용
        otherService.loadBoard(socketService.socket, (message: any) => {
          console.log('data Loaded : ', message);
          const temp = message.data.listTeams.items;
          Promise.all(temp).then((result) => setInfo(result));
        });
      }
    }
    handleDatabase();
  }, []);

  return (
    <div className="Ending">
      <div className="RankBoard">
        <div className="Ranktitle" onClick={() => history('/')}>
          RANKBOARD
        </div>
        <div className="Rankcontents">
          {info.map((item: any, index: any) => {
            if (index < 5) {
              return (
                <div className="RankString">
                  <div className="teamName">{item.title}</div>
                  <div className="teamMember">
                    (
                    {item.players.map((item: any) => {
                      return ' ' + item + '';
                    })}
                    )
                  </div>
                  <div className="teamScore">{item.score}점</div>
                </div>
              );
            }
            return '';
          })}
        </div>
      </div>
    </div>
  );
}
